<template>
  <div id='app'>
    <div  id="head_part" class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
    </div>
    <div id="video_part" class="row pt-3" style="border-top: 1px solid #666;">
        <div class="col-xs-12 col-md-9 pt-6 pl-7 pr-0">
        <div v-if="showflg" id="vid" class="pull-right mt-4" style="background-color: #1e1d1d;border-right: 1px solid #666">
        </div>

      </div>
        <div class="col-xs-12 col-md-3 pl-0 pr-7 pt-6" style="margin-top: 5px">
        <div class="col-xs-12 font-large font-bold video-list-title" style="color: wheat">播放列表&nbsp;<i class="el-icon-video-camera-solid" style="font-size: 20px;padding-top: 2px;"></i></div>
        <div class="col-xs-12 col-md-12 video-list-bg">
          <div class="row mt-3" @click="changeVideo(video.materialUrl, video.id, video)" v-for="(video, index) in videoList" v-bind:key="index" :class="[video.materialUrl === vUrl ?'video-selected':'video-bg']">
            <i class="el-icon-caret-left video-playing"></i>
            <div class="col-md-4 pl-0 pr-0">
              <img class="video-img" src="../../../assets/img/video-material.png">
            </div>
            <div class="col-md-8 pull-left pointer" >
              <div style="padding-top: 7%;font-size: 16px">{{video.materialName}}</div>
              <div class="cr-tm" style="padding-top: 3%;font-size: 12px;">2019-06-18</div>
            </div>
          </div>
        </div>
      </div>
      <div class="pl-7 col-md-10" style="padding-left: 10px;margin-top: 15px;">
        <span class="material-name font-bold">{{materialName}}</span>
      </div>
      <div class="material-src pl-7 col-md-9">
        <span class="pull-left" style="margin-left: unset">来源：<i class="iconfont iconlogo"></i></span>
        <span class="pull-right pointer"><a href="#comment_area" style="color: #ddd"><i class="el-icon-chat-dot-round"></i>评论</a></span>
        <span class="pull-right"><i class="el-icon-video-camera"></i>播放数:&nbsp;<span style="color: #00be06">{{vPlayCount}}</span></span>
      </div>
    </div>
    <div class="row">
      <div class="col-xs-12 col-md-8 col-md-offset-2 p-2">
        <div id="comment_area" class="pointer font-large font-bold mt-4 comment-title" >全部评论&nbsp;({{commentList.length}})</div>
        <div class="col-xs-12 col-md-12 p-2 mt-6 mb-6">
          <textarea  class="el-textarea__inner" placeholder="我来说点什么..." v-model="myComment" style="min-height: 200px;"></textarea>
          <div class="comment-btn pointer" @click="submitComment"><span>发布</span></div>
        </div>
        <div style="margin: 10px;padding: 10px">
          <p class="text-center" v-if="commentList.length==0"><img src="../../../assets/img/no_data1.png" style="width: 100%">暂无评论，我来发表第一篇评论！</p>
          <div v-else>
            <div v-for="(item,index) in commentList" v-bind:key="index">
              <div class="comment-per">
                <div class="col-md-1">
                  <img :src="item.userPhotoUrl" style="width: 100%;border-radius: 50%">
                </div>
                <div class="comment col-md-11" >
                  <div class="font-bold" v-if="item.commentId === null || item.commentId === ''">
                    {{item.discussantName}}&nbsp;<span>{{item.commentTime}}</span>
                    <div class="pointer" style="float: right;margin-right: 20px;"><a @click="showFatherCommentArea(item)"><i class="icondazhongicon05 iconfont font-bold font-large" :style="{ color: item.iconColor }"></i>&nbsp;<span :style="{ color: item.iconColor, fontWeight: '300', fontSize: '12px' }">回复</span></a></div>
                  </div>
                  <p v-if="item.commentId === null || item.commentId === ''" v-html="item.content">
                    <span v-if="item.stepName">#{{item.stepName}}</span>
                    <span v-if="item.materialName">#{{item.materialName}}</span>
                    {{item.content}}
                  </p>
                  <div v-if="commentReplyFlg" class="mt-3 mb-3">
                    <textarea v-if="item.newCommentReply" :placeholder="item.place" class="el-textarea__inner" v-model="item.newCommentReplyContent" style="min-height: 100px"></textarea>
                    <div v-if="item.newCommentReply" class="new-reply-btn pointer" @click="submitFatherComment(item)"><span>发布</span></div>
                  </div>
                  <div v-if="item.reply.length > 0" class="reply-list">
                    <list :comment="item" @submitSonComment="submitSonComment"></list>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!--回复区域 end-->
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../components/school-training/Header.vue'
import List from '../../../components/learning-library/commentList.vue'
export default {
  name: 'app',
  components: {
    Header,
    List
  },
  mounted () {
    $('#video_part').height(document.documentElement.clientHeight - $('#head_part').height() - 11)
    $('#video_part').css({
      'background-color': '#292929'
    })
    this.initData()
  },
  data () {
    return {
      vdHeight: '1',
      bdHeight: '',
      commentReplyFlg: false,
      marginTop: '',
      marteriaId: '',
      vUrl: '',
      vPlayer: null,
      vPlayCount: 0,
      showflg: true,
      videoList: [],
      docList: [],
      myComment: '',
      commentList: [],
      materialName: ''
    }
  },
  methods: {
    initData: function () {
      let me = this
      me.materialId = localStorage.getItem('materialId')
      me.materialName = localStorage.getItem('materialName')
      this.vUrl = localStorage.getItem('materialVideo')
      this.showflg = true
      me.vPlayer = polyvObject('#vid').videoPlayer({
        'wrap': '#vid',
        'width': document.documentElement.clientWidth * 0.698,
        'vid': me.vUrl
      })
      // 获取当前视频热度
      me.getVideoPlayCount(me.vUrl)
      console.log(me.vPlayCount)
      // 获取视频列表
      me.projectId = localStorage.getItem('projectId')
      me.$http.post('/doproject/materialsInfoByProject', { 'sourceType': 0, 'projectId': me.projectId }, res => {
        if (res.success) {
          for (var i = 0; i < res.data.length; i++) {
            if (res.data[i].materialType === 1) {
              me.videoList.push(res.data[i])
            } else if (res.data[i].materialType === 0) {
              me.docList.push(res.data[i])
            }
          }
        }
      })
      me.$http.post('/doproject/discussionTreeInfo', { 'projectId': localStorage.getItem('projectId') }, res => {
        // console.log(res)
        if (res.success) {
          me.commentList = res.data
        }
      })
    },
    loadDisscussionTree () {
      let me = this
      me.$http.post('/doproject/discussionTreeInfo', { 'projectId': localStorage.getItem('projectId') }, res => {
        if (res.success) {
          me.commentList = res.data
        }
      })
    },
    changeVideo (url, id, obj) {
      let me = this
      me.showflg = false
      me.$nextTick(() => {
        me.vUrl = url
        me.materialId = id
        me.materialName = obj.materialName
        me.showflg = true
        setTimeout(function () {
          me.vPlayer = polyvObject('#vid').videoPlayer({
            'wrap': '#vid',
            'width': document.documentElement.clientWidth * 0.698,
            'vid': me.vUrl
          })
          me.getVideoPlayCount(me.vUrl)
        })
      })
    },
    submitComment () {
      let me = this
      if (me.myComment === null || me.myComment === undefined || me.myComment === '') {
        this.$message('内容不能为空！')
        return false
      }
      let date = new Date()
      date = me.getNowTime(date)
      me.$http.post('/doproject/submitDiscussion', {
        'commentTime': date,
        'content': me.myComment,
        'projectId': localStorage.getItem('projectId'),
        'commentId': '',
        'materialId': me.materialId
      }, res => {
        me.$message('发布成功')
        me.myComment = null
        me.loadDisscussionTree()
      })
    },
    submitSonComment (data) {
      if (data.newReplyContent === null || data.newReplyContent === undefined || data.newReplyContent === '') {
        this.$message('内容不能为空！')
        return false
      }
      let me = this
      let date = new Date()
      date = me.getNowTime(date)
      me.$http.post('/doproject/submitDiscussion', {
        'commentTime': date,
        'content': data.newReplyContent,
        'projectId': localStorage.getItem('projectId'),
        'commentId': data.commentId,
        'materialId': me.materialId
      }, res => {
        me.$message('发布成功')
        me.loadDisscussionTree()
      })
    },
    submitFatherComment (obj) {
      if (obj.newCommentReplyContent === null || obj.newCommentReplyContent === undefined || obj.newCommentReplyContent === '') {
        this.$message('内容不能为空！')
        return false
      }
      let me = this
      let date = new Date()
      date = me.getNowTime(date)
      me.$http.post('/doproject/submitDiscussion', {
        'commentTime': date,
        'content': obj.newCommentReplyContent,
        'projectId': localStorage.getItem('projectId'),
        'commentId': obj.id,
        'materialId': me.materialId
      }, res => {
        // console.log(res)
        me.$message('发布成功')
        me.loadDisscussionTree()
      })
    },
    showFatherCommentArea (obj) {
      this.commentReplyFlg = false
      if (obj.newCommentReply === null || obj.newCommentReply === undefined) {
        obj.newCommentReply = true
        obj.place = '回复' + obj.discussantName
        obj.newCommentReplyContent = null
        obj.iconColor = '#6612cf'
      } else {
        obj.newCommentReply = !obj.newCommentReply
        if (obj.newCommentReply === false) {
          obj.iconColor = 'black'
        } else {
          obj.iconColor = '#6612cf'
        }
      }
      this.commentReplyFlg = true
    },
    getNowTime (date) {
      var Y = date.getFullYear() + '-'
      var M = date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) + '-' : date.getMonth() + 1 + '-'
      var D = date.getDate() < 10 ? '0' + date.getDate() + ' ' : date.getDate() + ' '
      var h = date.getHours() < 10 ? '0' + date.getHours() + ':' : date.getHours() + ':'
      var m = date.getMinutes() < 10 ? '0' + date.getMinutes() + ':' : date.getMinutes() + ':'
      var s = date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()
      let dateStr = Y + M + D + h + m + s
      return dateStr
    },
    // 获取视频热度
    getVideoPlayCount (vid) {
      let me = this
      let timestamp = Date.parse(new Date())
      // let start = '2019-01-01'
      // let end = getDateStr(0)
      // 查询视频观众量统计数据sign
      // var sign = 'endDate=' + end + '&ptime=' + timestamp + '&startDate=' + start + '&vid=' + vid + 'TkSZYq88An'
      // 查询视频播放量统计数据接口sign
      var sign = 'dr=this_year&period=monthly&ptime=' + timestamp + '&vid=' + vid + 'TkSZYq88An'
      sign = me.$sha(sign)
      sign = sign.toLocaleUpperCase()
      me.$axios({
        method: 'GET',
        // 查询视频观众量统计数据url
        // url: 'https://api.polyv.net/v2/data/visitor/56c20fd00b?ptime=' + timestamp + '&sign=' + sign + '&startDate=' + start + '&endDate=' + end + '&vid=' + vid,
        // 查询视频播放量统计数据接口url
        url: 'http://api.polyv.net/v2/videoview/56c20fd00b?ptime=' + timestamp + '&sign=' + sign + '&dr=this_year&period=monthly' + '&vid=' + vid,
        headers: {
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (res) => {
          let playCount = 0
          for (var i = 0; i < res.data.data.length; i++) {
            playCount = playCount + res.data.data[i].pcVideoView
          }
          me.vPlayCount = playCount
        })
    }
  }
}
</script>

<style scoped lang='scss'>
  div::-webkit-scrollbar{
    width:10px;
    height:10px;
    /**/
  }
  div::-webkit-scrollbar-track{
    background: #292929;
    border-radius:2px;
  }
  div::-webkit-scrollbar-thumb{
    background: #ffffff;
    border-radius:10px;
    width: 5px;
  }
  div::-webkit-scrollbar-corner{
    background: #179a16;
  }
  #video_part{
    color:#ddd;
    .material-name{
      color: white;
      font-size: 16px
    }
    .material-src{
      color:#ddd;
      margin-top:11px;
      span{
        margin-left: 10px;
        margin-right: 10px;
        i{
          margin-left: 5px;
          margin-right: 5px;
          font-weight: bold;
        }
      }
    }
  }
  .comment-per{
    border-bottom: 1px solid #ddd;
    margin: 10px;
    padding: 10px;
  }
  .video-list-title{
    margin-top: 10px;
    height: 40px;
    line-height: 40px;
    /*border: 1px solid #666;*/
    background-color: #1e1d1d;
    margin-top: 10px;
    border-bottom: none;
    border-left: none;
  }
  .video-list-bg{
    max-height:440px;
    min-height: 445px;
    /*border: 1px solid #666;*/
    background-color: #1e1d1d;
    border-left: none;
    overflow: auto;
    .video-img{
      padding: 5px;
      width: 100%;
      height: 70px;
    }
    .video-bg{
      color:#ddd;
      .cr-tm{
        color: #666;
      }
    }
    .video-bg:hover {
      /*background-color: rgba(255,255,255,0.7);*/
      /*color: #00be06;*/
      color: wheat;
      .cr-tm{
        /*color: #00be06;*/
        color: wheat;
      }
    }
    .video-selected{
      /*background-color: rgba(255,255,255,0.6);*/
      /*color:#00be06;*/
      font-weight: bold;
      /*border:2px solid #00be06;*/
      color: wheat;
      border:2px solid wheat;
      .video-img {
      }
      .video-playing{
        display: block;
      }
    }
    .video-playing{
      display: none;
      color: wheat;
      position: absolute;
      font-size: 20px;
      font-weight: bold;
      left: -3px;
      margin-top: 10%;
    }
  }
  .comment-title{
    border-bottom: 2px solid wheat;
    height: 40px;
  }
  .comment{
    p{
      padding-top: 10px;
      padding-bottom: 10px;
    }
    padding: 20px;
    .reply-list{
      background-color: #ddd;
      padding: 20px;
    }
  }
  .comment-btn{
    width: 55px;
    height: 25px;
    line-height: 25px;
    background: #6612cf;
    text-align: center;
    border-radius: 15px;
    color: white;
    position: relative;
    margin-top: -30px;
    float: right;
    margin-right: 25px;
  }
</style>
